ader{\n    position: fixed;\n    width: 100%;\n    height: 100%;\n    top: 0;\n    left: 0;\n \n    z-index: 1;\n    transition: all 3s ease-out;\n}\n.top{\n    top:0\n}\n\n.bottom{\n    bottom: 0;\n}\n.spinner-block{\n    width: 200px;\n    height: 200px;\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%,-50%);\n    display: inline-block;\n    overflow: hidden;\n}\n.block{\n    width: 100%;\n    height: 100%;\n    position: relative;\n    transform: translateZ(0) scale(1);\n    backface-visibility: hidden;\n    transform-origin: 0 0;\n}\n.block div{\n    position: absolute;\n    width: 40px;\n    height: 40px;\n    background: #151233;\n    animation: block 1s linear infinite;\n    box-sizing: content-box;\n}\n\n.block div:nth-child(1){\n    left: 38px;\n    top:38px;\n    animation-delay: 0s;\n\n}\n.block div:nth-child(2){\n    left: 80px;\n    top:38px;\n    animation-delay: 0.125s;\n    \n}\n.block div:nth-child(3){\n    left: 122px;\n    top:38px;\n    animation-delay: 0.25s;\n    \n}\n.block div:nth-child(4){\n    left: 38px;\n    top:80px;\n    animation-delay: 0.875s;\n    \n}\n.block div:nth-child(5){\n    left: 122px;\n    top:80px;\n    animation-delay: 0.375s;\n    \n}\n.block div:nth-child(6){\n    left: 38px;\n    top:122px;\n    animation-delay: 0.75s;\n    \n}\n\n.block div:nth-child(7){\n    left: 80px;\n    top:122px;\n    animation-delay: 0.625s;\n    \n}\n\n.block div:nth-child(8){\n    left: 122px;\n    top:122px;\n    animation-delay: 0.5s;\n    \n}\n\n@keyframes block{\n    0%{\n        background: #E96767;\n    }\n\n    12.5%{\n        background: #E96767;\n    }\n    12.625%{\n        background: #151233;\n    }\n    100%{\n        background: #151233;\n    }\n}\n\n</style>"],"sourceRoot":""}]);
