import { container, inject } from "../../../domain/core/diContainer";
import * as VueRouter from "vue-router";
import { IRouter, IRouteDefinition } from "../../../domain/presentation/IRouter";
import { routes } from "./routes";
import { IUseCaseExecutor } from "../../../domain/useCase/IUseCaseExecutor";
import { ILogger } from "../../../domain/infrastructure/ILogger";
import { IConfig } from "../../../domain/infrastructure/IConfig";
import { IViewModel } from "../../../domain/viewModel/IViewModel";
import { IOrganizationUseCase } from "../../../domain/useCase/IOrganizationUseCase";
import { IUserUseCase } from "../../../domain/useCase/IUserUseCase";

interface IRouterMeta extends VueRouter.RouteMeta, IRouteDefinition {
    popstate: boolean;
}
let isPopstate = false;
window.addEventListener('popstate', popEvent => isPopstate = true);
export class RouterImpl implements IRouter {
    private readonly router: VueRouter.Router;
    constructor(
        private readonly executor: IUseCaseExecutor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor),
        readonly logger: ILogger = container.resolve<ILogger>(ILogger),
        readonly viewModel: IViewModel = container.resolve<IViewModel>(IViewModel),
        readonly organization: IOrganizationUseCase = container.resolve<IOrganizationUseCase>(IOrganizationUseCase),
        readonly userUseCase: IUserUseCase = container.resolve<IUserUseCase>(IUserUseCase),
        readonly config = container.resolve<IConfig>(IConfig),
    ) {
        this.router = VueRouter.createRouter({
            history: VueRouter.createWebHistory(config.basePath),
            routes: routes as Array<VueRouter.RouteRecordRaw>,
        });

        this.router.beforeEach((to, from, next) => {
            this.executor.execute(async () => {
                const userProfilePath = "/completeProfile";
                if (to.fullPath != userProfilePath) {
                    const checkResult = await userUseCase.checkUserForUpdateNeeds();
                    if (checkResult.needProfileUpdate) {
                        (window as any).studio_redirectTo = from.fullPath;
                        this.router.push({path: userProfilePath, params: {  redirectTo: from.fullPath }});
                    }
                }
                const routeOrgId = to.params.orgId as string | undefined;
                if (routeOrgId && viewModel.user?.selectedOrganization?.id != routeOrgId) {
                    await executor.execute<void>(async () => { await this.organization.changeOrganization(routeOrgId); }, { loading: true });
                }
                if (!(to.meta as IRouterMeta).noAuth) {
                    // SSO-TODO: check this
                    // if (!await this.executor.executeUseCase<ICheckLogin>(ICheckLogin, { via: "client" })) {
                    //     this.logger.log({ level: "error", message: `missing authentication for '${to.path}' assuming timeout.` });
                    //     this.executor.execute(async () => sessionManager.setTimedOut(), { loading: false });
                    //     return;
                    // }
                }

                next();
            }, { loading: true });
        });
        this.router.afterEach((to, from, fail) => {
            (to.meta as IRouterMeta).popstate = isPopstate;
            isPopstate = false;
        });
    }

    public async push(to: string): Promise<void> {
        await this.router.push(to);
    }

    public back() {
        this.router.back();
    }

    public getInternalRouter() {
        return this.router;
    }

    public getRouteParams<T extends VueRouter.RouteParams>() {
        const route = VueRouter.useRoute();
        return route.params as T;
    }
    private getRouteMeta<T extends VueRouter.RouteMeta>() {
        const route = VueRouter.useRoute();
        return route.meta as T;
    }
    public isBackOrForward() {
        return this.getRouteMeta<IRouterMeta>().popstate || false;
    }
}
